html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: 'Druk';
  src: url("../fonts/Druk_Wide_Medium_Cy.otf") format("truetype");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Artegra';
  src: url("../fonts/ArtegraSans-Regular.ttf") format("truetype");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Artegra Medium';
  src: url("../fonts/ArtegraSans-Medium.ttf") format("truetype");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Artegra Semibold';
  src: url("../fonts/ArtegraSans-SemiBold.ttf") format("truetype");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

button {
  outline: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}

.button {
  border-radius: 50%;
  background: #A03ADB;
  font-family: 'Artegra Medium', sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  color: #F1F1F1;
}

.button.advantages-button {
  width: 18.4vw;
  height: 18.4vw;
  opacity: 1;
  font-size: 1.7vw;
  margin: 0 auto;
  display: block;
  margin-top: 7vh;
}

.button.plan-button {
  width: 14.1vw;
  height: 14.1vw;
  opacity: 1;
  font-size: 1.6vw;
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}

.button .button-rewealer {
  width: 101%;
  height: 101%;
  position: absolute;
  background: white;
  top: 50%;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  border-radius: 50%;
}

.button .button-text .word {
  display: inline-block;
}

.button .button-text .word .char {
  display: inline-block;
  transform: translateY(30px);
  opacity: 0;
}

.button.footer-button {
  width: 17vw;
  height: 17vw;
  font-size: 1.6vw;
}

body {
  font-family: 'Artegra', sans-serif;
  font-weight: 300;
  font-size: 1.55vw;
  color: #131116;
  background: #F1F1F1;
}

*::selection {
  background: #A03ADB;
  color: white;
}

.header {
  width: 100%;
  height: 100vh;
  background: #131116;
  color: white;
  position: relative;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__logo {
  position: absolute;
  top: 4%;
}

.header__social {
  position: absolute;
  bottom: 4%;
}

.h1 {
  font-size: 5.2vw;
  line-height: 1.3em;
}

.h1__second-line {
  display: block;
  margin-left: 11.5vw;
}

.h1__third-line {
  display: block;
}

.purple {
  color: #A03ADB;
}

.social {
  font-size: 18px;
  display: flex;
}

.social__link {
  margin-right: 56px;
}

.scroll-down {
  background: #A03ADB;
  width: 14vw;
  height: 14vw;
  border-radius: 50%;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  right: 8.6vw;
  top: 7.68vh;
  text-transform: uppercase;
  font-family: 'Artegra Semibold', sans-serif;
  font-weight: 600;
  font-size: 1.25vw;
  text-align: center;
}

.scroll-down__arrow {
  position: absolute;
  bottom: 15%;
}

.arrow-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.arrow-block__right {
  margin-left: 16.7vw;
}

.arrow-block__arrow-circle {
  width: 23vw;
  height: 23vw;
  border-radius: 50%;
  background: #A03ADB;
  display: flex;
  justify-content: center;
  align-items: center;
}

.arrow-block__arrow-circle svg {
  display: block;
}

.arrow-block__p {
  font-family: 'Artegra Medium', sans-serif;
  font-weight: 500;
  font-size: 2.7vw;
  line-height: 1.65em;
  letter-spacing: 0.01em;
  flex-direction: row-reverse;
  position: relative;
}

.arrow-block-reverse {
  flex-direction: row-reverse;
}

.arrow-block-reverse .arrow-block__right {
  margin-left: 0px;
}

.arrow-block-reverse .arrow-block__left {
  margin-left: 16.7vw;
}

.arrow-block-reverse .arrow-block__arrow-circle svg {
  transform: rotate(180deg);
}

.list-items {
  margin-top: 130px;
  position: relative;
}

.list-items__line {
  width: 100%;
  height: 3px;
  background: #131116;
}

.list-items__item:last-child::after {
  content: '';
  width: 100%;
  height: 3px;
  background: #131116;
  display: block;
}

.list-items__text {
  display: inline-block;
  padding-top: 5.4vh;
  padding-bottom: 5.4vh;
  line-height: 1.5em;
  padding-right: 5.2vw;
}

.list-items.with-padding {
  padding-left: 22.7vw;
}

.list-items__text-wrapper {
  display: flex;
  align-items: center;
}

.list-items__text-wrapper svg {
  margin-right: 5vw;
  fill: #131116;
  width: 8%;
}

.list-items__text-wrapper .list-items__text {
  width: 90%;
}

.black-block {
  background: #131116;
  color: white;
  padding-top: 9.2vh;
  padding-bottom: 9.2vh;
}

.black-block__big-p {
  font-size: 2.2vw;
  font-family: 'Druk', 'Arial', sans-serif;
  font-weight: 700;
  line-height: 1.6em;
}

.black-block h2 {
  max-width: 100%;
}

.black-block__p {
  margin-top: 28px;
}

.question {
  background: #A03ADB;
  color: white;
  padding: 7px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  line-height: 1em;
  position: absolute;
  top: -20px;
  margin-left: -10px;
  cursor: pointer;
}

.question::after {
  content: '?';
  display: inline-block;
  margin-top: 3px;
}

.line-through {
  text-decoration: line-through;
}

h1, h2 {
  font-family: 'Druk', 'Arial', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}

h2 {
  max-width: 1273px;
  line-height: 1.6em;
  font-size: 2.7vw;
}

h2.because {
  max-width: 59vw;
}

.descriptor {
  font-family: 'Artegra Semibold', sans-serif;
  font-weight: 600;
  font-size: 32px;
  margin-top: 25px;
  max-width: 45vw;
  line-height: 1.6em;
}

.container {
  padding-left: 5.2vw;
  padding-right: 5.2vw;
  width: 100%;
  height: 100%;
}

.container.zero-right-padding {
  padding-right: 0;
}

.section {
  position: relative;
}

.section__container {
  padding-top: 18.5vh;
}

.section__container--last {
  padding-bottom: 18.5vh;
}
